"use client";
import React from 'react';
import { useToast } from './ToastContext';

export default function ToastView() {
  const { toasts, removeToast } = useToast();

  if (!toasts.length) return null;

  return (
    <div className="fixed right-4 bottom-4 z-50 flex flex-col gap-2">
      {toasts.map((t) => (
        <div key={t.id} className={`p-3 rounded-md shadow-md max-w-sm ${t.type === 'success' ? 'bg-green-50 text-green-800' : t.type === 'error' ? 'bg-red-50 text-red-800' : 'bg-blue-50 text-blue-800'}`}>
          <div className="flex items-start justify-between gap-4">
            <div className="text-sm">{t.message}</div>
            <button onClick={() => removeToast(t.id)} className="text-xs text-gray-500">Dismiss</button>
          </div>
        </div>
      ))}
    </div>
  );
}
